<template>
  <!-- 发布职位 -->
  <div class="release-wrap">
    <div class="release-box">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
        label-position='left'
      >
      <!-- 职位信息 start -->
        <div class="release-panel">
          <div class="release-tit">
            <label>1</label>
            <h4>职位信息</h4>
            <p>加*内容，在确认发布成功后，将无法修改</p>
          </div>
          <el-form-item label="公司" prop="name">
              兰州卫星环保科技有限公司
          </el-form-item>
          <el-form-item label="招聘类型" prop="recruitType">
            <el-select
              v-model="ruleForm.recruitType"
              placeholder="请选择招聘类型"
            >
              <el-option label="区域一" value="shanghai"></el-option>
              <el-option label="区域二" value="beijing"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="职位名称" prop="positionName">
            <el-input v-model="ruleForm.positionName" placeholder="请输入职位名称"></el-input>
          </el-form-item>
          <el-form-item label="工作城市" prop="workCity">
            <city v-model="ruleForm.workCity"></city>
          </el-form-item>
          <el-form-item label="工作地点" prop="workAdr">
            <el-input v-model="ruleForm.workAdr" placeholder="请输入工作地点"></el-input>
          </el-form-item>
        </div>
      <!-- 职位信息 end -->
      <!-- 职位要求 start -->
       <div class="release-panel">
          <div class="release-tit">
            <label>2</label>
            <h4>职位要求</h4>
            <p>我们将通过以下条件，为您精确推荐合适的牛人，请尽量详细填写</p>
          </div>
          <el-form-item label="经验" prop="expReq">
            <el-select
              v-model="ruleForm.expReq"
              placeholder="请选择经验要求"
            >
              <el-option label="区域一" value="shanghai"></el-option>
              <el-option label="区域二" value="beijing"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="学历" prop="eduReq">
            <el-select
              v-model="ruleForm.eduReq"
              placeholder="请选择学历"
            >
              <el-option label="区域一" value="shanghai"></el-option>
              <el-option label="区域二" value="beijing"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item class="multiple" label="薪资范围" prop="salaryReq">
            <el-input v-model="ruleForm.salaryReq" placeholder="最低薪资"></el-input>
            <el-input v-model="ruleForm.salaryReq" placeholder="最高薪资"></el-input>
          </el-form-item>
          <el-form-item label="工作要求" prop="postDesc">
            <el-input v-model="ruleForm.postDesc" :maxlength="200" :rows="7" type="textarea" placeholder="1、工作内容；2、任职要求；3、特别说明"></el-input>
          </el-form-item>
          <el-form-item label="职位关键词" prop="postKey">
            <el-input v-model="ruleForm.postKey"  placeholder="请输入关键词，最长6个字，多个请用,隔开"></el-input>
          </el-form-item>
          <el-form-item>
              <el-button @click="submitForm('ruleForm')" type="primary">发布</el-button>
          </el-form-item>
       </div>
      <!-- 职位要求 end -->
      </el-form>
    </div>
  </div>
</template>
<script>
import City from '../../components/city.vue';
export default {
  name: "release",
  components:{City},
  data() {
    return {
      rules: {
        recruitType:[{ required: true, message: '请选择招聘类型', trigger: 'blur' }],
        positionName:[
          { required: true, message: '请输入职位名称', trigger: 'blur' },
          { max: 20, message: '长度限制最长20个字', trigger: 'blur'}
        ],
        workCity:[{ required: true, message: '请选择工作城市', trigger: 'blur' }],
        workAdr:[
          { required: true, message: '请输入工作地点', trigger: 'blur' },
          { max: 20, message: '长度限制最长20个字', trigger: 'blur'}
        ],
      },
      ruleForm: {
        recruitType:'',//招聘类型
        positionName:'',//职位名称
        workCity:[],//工作城市
        workAdr:'',//工作地点
        expReq:"",//经验要求
        eduReq: "",//学历要求
        salaryReq:'',//薪酬范围
        postDesc:'',//岗位描述
        postKey:'',//岗位关键词
      },
    };
  },
  mounted(){
  },
  methods:{
    submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      }
  }
};
</script>
<style lang='less' scoped>
.release-wrap {
  background: #f6f6f8;
  padding: 20px 0;
}
.release-box {
  width: 1200px;
  margin: 0 auto;
  background: #fff;
  padding: 20px;
  box-sizing: border-box;
  text-align: left;
  /deep/ .el-input {
      width: 300px;
  }
  /deep/ .el-form-item{
      &.multiple {
          .el-input {
              width: 150px;
          }
      }
      .el-textarea {
          width: 472px;
        //   height: 100px;
      }
  }
}
</style>


