<template>
    <el-cascader
    v-model="val"
    :options="options"
    :props="props"
    @change="change"
    clearable></el-cascader>
</template>
<script>
import data from '../utils/city'
export default {
    name:'city',
    data(){
        return{
            val:'',
            props:{
                label:'name',
                value:'name'
            }
        }
    },
    props:{
        value:Array
    },
    computed:{
        options(){
            return data();
        }
    },
    mounted(){
        this.val = this.value;
    },
    methods:{
        change(v){
            this.$emit('input',v);
        }
    }
}
</script>


